<template>
  <v-container
    id="listarVehiculos"
    fluid
    tag="section"
  >
    <v-btn
      to="/adv/vehiculos/nuevo"
      fab
      dark
      color="purple"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>

    <v-btn
      to="/adv"
      fab
      dark
      class="mx-10"
    >
      <v-icon>mdi-arrow-left</v-icon>
    </v-btn>

    <base-material-card
      icon="mdi-clipboard-text"
      title="Registro de Vehiculos"
      class="px-5 py-3"
    >
      <v-data-table
        :headers="cabecera"
        :items="vehiculos"
        sort-by="Modelos"
        class="elevation-5 my-5"
      >
        <template v-slot:item.status="{item}">
          <!--Btn Chips-->
          <v-chip
            :color="getColor(item.status)"
            dark
          >
            {{ item.status }}
          </v-chip>
        </template>

        <template v-slot:item.actions="{ item }">
          <!--Btn Asignar-->
          <v-btn
            :to="{ name: 'Editar Vehiculo', params: { id: item._id } }"
            fab
            small
            color="pink"
          >
            <v-icon>mdi-car-key</v-icon>
          </v-btn>
          <!--Btn Editar-->
          <v-btn
            v-show="false"
            fab
            small
            color="light-blue"
          >
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
          <!--Btn Eliminar-->
          <v-btn
            v-show="false"
            fab
            small
            color="red"
            class="mx-5"
          >
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </base-material-card>

    <v-dialog
      v-model="dialog"
      width="350"
    >
      <v-card>
        <v-card-title class="headline grey lighten-2">
          ¿Desea eliminar el registro?
        </v-card-title>

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn @click="dialog = false">
            Cancelar
          </v-btn>
          <v-btn
            color="error"
            @click="confirmarBorrado(id)"
          >
            Aceptar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  import axios from 'axios'
  import Vue from 'vue'
  import moment from 'moment'

  const urlVehiculo = 'https://sistema-adv.herokuapp.com/api/vehicles/'

  Vue.filter('fechaFormateada', function (value) {
    if (value) {
      return value.toISOString().substr(0, 4)
    }
  })

  export default {
    name: 'ListarVehiculos',
    data () {
      return {
        vehicles: null,
        dialog: false,
        cabecera: [
          { text: 'Marca', value: 'brand', align: 'center' },
          { text: 'COD_TDP', value: 'cod_tdp', align: 'center' },
          { text: 'SERIE_TDP', value: 'serie_tdp', align: 'center' },
          { text: 'Modelo', value: 'model', align: 'center' },
          { text: 'Color', value: 'colour', align: 'center' },
          { text: 'Año Modelo', value: 'model_year', align: 'center' },
          { text: 'Ubicación', value: 'location', align: 'center' },
          { text: 'Condición', value: 'status', align: 'center' },
          {
            text: 'Acciones',
            value: 'actions',
            align: 'center',
            sortable: false,
          },
        ],
        vehiculos: [
          {
            brand: '',
            cod_tdp: '',
            serie_tdp: '',
            category: '',
            model: '',
            colour: '',
            manufacturing_year: '',
            model_year: '',
            brand_office: '',
            location: '',
            price: '',
            status: '',
          },
        ],
      }
    },
    mounted () {
      this.obtenerVehiculos()
    },
    methods: {
      obtenerVehiculos () {
        axios
          .get(urlVehiculo)
          .then((res) => {
            this.vehiculos = res.data
            this.vehicles = res.data
          })
          .catch((e) => {
            console.log(e)
          })
      },
      confirmarBorrado (id) {},
      getColor (status) {
        if (status === 'Libre') return 'green'
        else if (status === 'Asignado') return 'orange'
        else return 'red'
      },
    },
  }
</script>
